// Select
//
// Replaces the browser default select with a custom one, mostly pulled from
// https://primer.github.io/.

.form-select {
  display: block;
  width: 100%;
  padding: css-var(--#{$variable-prefix}form-select-padding-y, $form-select-padding-y) css-var(--#{$variable-prefix}form-select-indicator-padding, $form-select-indicator-padding) css-var(--#{$variable-prefix}form-select-padding-y, $form-select-padding-y) css-var(--#{$variable-prefix}form-select-padding-x, $form-select-padding-x);
  -moz-padding-start: subtract($form-select-padding-x, 3px); // See https://github.com/twbs/bootstrap/issues/32636
  font-family: css-var(--#{$variable-prefix}form-select-font-family, $form-select-font-family);
  @include font-size($form-select-font-size);
  font-weight: css-var(--#{$variable-prefix}form-select-font-weight, $form-select-font-weight);
  line-height: css-var(--#{$variable-prefix}form-select-line-height, $form-select-line-height);
  color: css-var(--#{$variable-prefix}form-select-color, $form-select-color);
  background-color: css-var(--#{$variable-prefix}form-select-bg, $form-select-bg);
  background-image: escape-svg($form-select-indicator);
  background-repeat: no-repeat;
  background-position: css-var(--#{$variable-prefix}form-select-bg-position, $form-select-bg-position);
  background-size: css-var(--#{$variable-prefix}form-select-bg-size, $form-select-bg-size);
  border: $form-select-border-width solid $form-select-border-color;
  @include border-radius($form-select-border-radius, 0);
  @include box-shadow($form-select-box-shadow);
  @include transition($form-select-transition);
  appearance: none;

  &:focus {
    border-color: css-var(--#{$variable-prefix}form-select-focus-border-color, $form-select-focus-border-color);
    outline: 0;
    @if $enable-shadows {
      @include box-shadow($form-select-box-shadow, $form-select-focus-box-shadow);
    } @else {
      // Avoid using mixin so we can pass custom focus shadow properly
      box-shadow: css-var(--#{$variable-prefix}form-select-focus-box-shadow, $form-select-focus-box-shadow);
    }
  }

  &[multiple],
  &[size]:not([size="1"]) {
    padding-right: css-var(--#{$variable-prefix}form-select-padding-x, $form-select-padding-x);
    background-image: none;
  }

  &:disabled {
    color: css-var(--#{$variable-prefix}form-select-disabled-color, $form-select-disabled-color);
    background-color: css-var(--#{$variable-prefix}form-select-disabled-bg, $form-select-disabled-bg);
    border-color: css-var(--#{$variable-prefix}form-select-disabled-border-color, $form-select-disabled-border-color);
  }

  // Remove outline from select box in FF
  &:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 $form-select-color;
  }
}

.form-select-sm {
  padding-top: css-var(--#{$variable-prefix}form-select-padding-y-sm, $form-select-padding-y-sm);
  padding-bottom: css-var(--#{$variable-prefix}form-select-padding-y-sm, $form-select-padding-y-sm);
  padding-left: css-var(--#{$variable-prefix}form-select-padding-x-sm, $form-select-padding-x-sm);
  @include font-size($form-select-font-size-sm);
  @include border-radius($form-select-border-radius-sm);
}

.form-select-lg {
  padding-top: css-var(--#{$variable-prefix}form-select-padding-y-lg, $form-select-padding-y-lg);
  padding-bottom: css-var(--#{$variable-prefix}form-select-padding-y-lg, $form-select-padding-y-lg);
  padding-left: css-var(--#{$variable-prefix}form-select-padding-x-lg, $form-select-padding-x-lg);
  @include font-size($form-select-font-size-lg);
  @include border-radius($form-select-border-radius-lg);
}
